.navbar-container {
    width: 100%;
    height:88px;
    color:white;
    background: black;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}
.doggo {
    height: 75px;
    width: 70px;
    margin-right: auto;
    margin-left: auto;
}
.navbar-links {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 1%;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-item-align: end;
        align-self: flex-end;
}
.navbar-links a:hover{
    
    /* text-decoration:underline; */
    }
    a {
        color:white;
    }
 .nav-link-holder > a:link, .nav-link-holder > a:hover, .nav-link-holder > a:visited, .nav-link-holder > a:active {
    text-decoration: none;
}

.nav-link-holder > .selected {
    color:red;
    padding-bottom: 6%;
    border-bottom: 3px solid red;
}



